<template>
    <div ref="channelListBulk" class="channelListBulk">
        <van-pull-refresh v-model="refreshing" :head-height="80" @refresh="onRefresh"
            :style="{ 'min-height': containerHeight + 'px' }">
            <van-list v-model:loading="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
                <div class="top" :style="{ 'width': '100% ', 'height': topHeight }"></div>
                <div class="box">
                    <slot name="swipe"></slot>
                    <newSubmitList :ArticleList="showList"></newSubmitList>
                </div>
                <div class="bottom" :style="{ 'width': '100% ', 'height': bottomHeight }"></div>
            </van-list>
        </van-pull-refresh>
    </div>
</template>
<script>
import newSubmitList from '@/components/newSubmitList.vue'
import { Toast } from 'vant'
import { useRect } from '@vant/use';
export default {
    name: 'ChannelList',
    inject: ['minHeight'],
    props: {
        channelId: {
            type: Number,
        },
        currentChannel: {
            type: Number,
        },
        params: {
            type: Object,
        },
        cSize: {
            type: Number
        },
        startIndex: {
            type: Number
        },
        itemHeight: {
            type: Number
        },
        containerHeight: {
            type:Number
        }
    },
    components: {
        newSubmitList,
    },
    data() {
        return {
            loading: false,
            finished: false,
            size: this.$store.state.size,
            // size: 20,
            skip: 0,
            list: [],
            refreshing: false,
            top: 0,
        }
    },
    methods: {
        onRefresh() {
            if (this.loading === true) {
                Toast({
                    overlay: true,
                    message: '勿在加载时刷新!'
                })
                this.refreshing = false;
                return
            }
            this.loading = true;
            this.finished = false;
            this.skip = 0;
            this.onLoad();
        },
        onLoad() {
            if (this.channelId === this.currentChannel) {
                const params = { ...this.params, skip: this.skip, size: this.size }
                this.$axios.get('articlesList', { params }).then(rs => {
                    const timer = setTimeout(() => {
                        if (this.refreshing) {
                            this.list = [...rs];
                            Toast('刷新成功');
                            this.refreshing = false;
                        } else {
                            this.list = [...this.list, ...rs,];
                        }
                        this.loading = false;
                        if (rs.length < this.size) {
                            this.finished = true;
                        }
                        else
                            this.skip = this.skip + this.size
                        clearTimeout(timer)
                    }, 1000)
                })
            }
        },
        getHeight() {
            return { 'min-height': `calc(100vh - 50px - ${this.top + 'px'})` }
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.top = useRect(this.$refs.channelListBulk).top;
        })
    },
    computed: {
        endIndex() {
            let endIndex = this.startIndex + this.cSize * 2;
            if (!this.list[endIndex]) {
                endIndex = this.list.length
            }
            return endIndex
        },
        showList() {
            let startIndex;
            if (this.startIndex <= this.cSize) {
                startIndex = 0;
            }
            else {
                startIndex = this.startIndex - this.cSize;
            }
            return this.list.slice(startIndex, this.endIndex)
        },
        topHeight() {
            let startIndex = 0;
            if (this.startIndex <= this.cSize) {
                startIndex = 0;
            }
            else {
                startIndex = this.startIndex - this.cSize;
            }
            return startIndex * this.itemHeight + 'px'
        },
        bottomHeight() {
            return (this.list.length - (this.endIndex)) * this.itemHeight + 'px'
        },
        paddingStyle() {
            let startIndex = 0;
            if (this.startIndex <= this.cSize) {
                startIndex = 0;
            }
            else {
                startIndex = this.startIndex - this.cSize;
            }
            return {
                'padding-top': startIndex * this.itemHeight + 'px',
                'padding-bottom': (this.list.length - (this.endIndex)) * this.itemHeight + 'px'
            }
        }
    }

}
</script>

