<template>
  <div class="retroSnake wrapper ">
    <div class="gameBox" style=" padding: 10px;border: 10px black solid;" ref="gameBox">
      <div class="stage" :style="stageStyle">
        <div class="snake" ref="snake">
          <div :style="index === head ? snakeHeadStyle : snakeItemStyle(index)" v-for="(el, index) in length"
            :key="index" class="snakeItem"></div>
        </div>
        <div class="food" :style="foodStyle">
          <div class="slice" :style="sliceStyle"></div>
          <div class="slice" :style="sliceStyle"></div>
          <div class="slice" :style="sliceStyle"></div>
          <div class="slice" :style="sliceStyle"></div>
        </div>
      </div>
      <div class="info" :style="infoStyle">
        <span>SCORE:{{ score }}</span>
        <span>LEVEL:{{ level }}</span>
      </div>
      <div class="handler">
        <div>
          <div class="btnBox">
            <van-button icon="arrow-up" type="primary" color="#74856c" @click="directionChange({ key: 'ArrowUp' })" />
          </div>
        </div>
        <div>
          <span class="btnBox">
            <van-button icon="arrow-left" type="primary" color="#74856c"
              @click="directionChange({ key: 'ArrowLeft' })" />
          </span>
          <div style="padding:0 10px;display: flex;justify-content: center;align-items: center;">
            <van-button icon="circle" type="primary" color="black" round />
          </div>
          <div class="btnBox">
            <van-button icon="arrow" type="primary" color="#74856c" @click="directionChange({ key: 'ArrowRight' })" />
          </div>
        </div>
        <div>
          <div class="btnBox">
            <van-button icon="arrow-down" type="primary" color="#74856c"
              @click="directionChange({ key: 'ArrowDown' })" />
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
export default {
  name: 'RetroSnake',
  components: {
  },
  data() {
    return {
      boxWidth: 0,
      boxHeight: 0,
      unit: 10,
      stageWidth: 0,
      stageHeight: 0,
      snakeSituation: {
        left: 0,
        top: 0
      },
      foodSituation: {
        left: 0,
        top: 0
      },
      direction: null,
      timer: null,
      keyArr: ['ArrowUp', 'ArrowDown', 'ArrowLeft', 'ArrowRight'],
      reObj: { ArrowUp: 'ArrowDown', ArrowDown: 'ArrowUp', ArrowLeft: 'ArrowRight', ArrowRight: 'ArrowLeft' },
      length: 1,//蛇的长度
      head: 0,//头的索引
      css: {},
      score: 0,
      level: 1,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    init() {//初始化
      this.getSize();
      this.getFoodSituation();
    },
    foodMeet() {//碰见食物
      this.getFoodSituation();
      this.length++;
      this.score += 100;
      if (this.score % 300 === 0) {
        this.level++;
        this.move();
      }
      this.css[this.length - 1] = { left: null, top: null }
      this.css[this.length - 1].left = this.snakeSituation.left;
      this.css[this.length - 1].top = this.snakeSituation.top;
    },
    getFoodSituation() {//改变食物位置
      this.foodSituation.left = ~~(Math.random() * this.stageWidth / this.unit) * this.unit;
      this.foodSituation.top = ~~(Math.random() * this.stageHeight / this.unit) * this.unit;
      console.log(this.foodSituation,this.stageWidth,this.stageHeight,this.boxHeight);
    },
    getSize() {//获取容器和设置舞台宽高
      this.boxWidth = this.$refs.gameBox.clientWidth;
      this.boxHeight = this.$refs.gameBox.clientHeight;
      this.stageWidth = ~~((this.boxWidth - 20) / this.unit) * this.unit;
      this.stageHeight = ~~((this.boxHeight - 20) * 0.65 / this.unit) * this.unit;
    },
    directionChange(event) {//方向改变
      if (this.keyActive && this.keyArr.indexOf(event.key) > -1 && this.direction !== event.key) {
        this.keyActive = false;
        if (this.length < 2 || this.reObj[this.direction] !== event.key) {
          this.direction = event.key
        }
      }
    },
    snakeItemStyle(index) {//设置蛇身体的位置
      return {
        width: 10 + 'px', height: 10 + 'px',
        left: this.css[index] ? this.css[index].left + 'px' : 0, top: this.css[index] ? this.css[index].top + 'px' : 0
      }
    },
    isGameOver() {//判断游戏是否结束
      let flag = this.snakeSituation.top < 0 || this.snakeSituation.top > this.stageHeight - this.unit
        || this.snakeSituation.left < 0 || this.snakeSituation.left > this.stageWidth - this.unit
      for (let i in this.css) {
        if (+i === this.head)
          continue;
        else {
          if (this.css[i].left === this.snakeSituation.left && this.css[i].top === this.snakeSituation.top) {
            console.log(this.head, i, this.css[i].left, this.css[i].top, this.snakeSituation.left, this.snakeSituation.top);
            flag = true;
          }
        }
      }
      if (flag) {
        clearInterval(this.timer);
        for (let i in this.css) {
          this.css[i].left = ~~(Math.random() * this.stageWidth / this.unit) * this.unit;
          this.css[i].top = ~~(Math.random() * this.stageHeight / this.unit) * this.unit;
        }
        Toast.fail({
          message: 'YOU DEAD!',
          onClose: (() => {
            if (confirm('重新开始?')) {
              this.$emit('childReload');
            }
            else {
              this.$router.back();
            }
          })
        })

      }
    }, // 游戏结束判断
    move() {//移动
      clearInterval(this.timer)
      this.timer = setInterval(() => {
        if (this.direction) {
          //保存上个头的位置
          this.css[this.head] = { left: null, top: null }
          this.css[this.head].left = this.snakeSituation.left;
          this.css[this.head].top = this.snakeSituation.top;
          this.head--;//新的头产生
          if (this.head < 0) {
            this.head = this.length - 1
          }
          switch (this.direction) {
            case 'ArrowUp':
              this.snakeSituation.top -= this.unit
              break;
            case 'ArrowDown':
              this.snakeSituation.top += this.unit
              break;
            case 'ArrowLeft':
              this.snakeSituation.left -= this.unit
              break;
            case 'ArrowRight':
              this.snakeSituation.left += this.unit
              break;
          }
          this.isGameOver();
          if (this.foodSituation.left === this.snakeSituation.left &&
            this.foodSituation.top === this.snakeSituation.top) {
            this.foodMeet()
          }
        }
        this.keyActive = true;
      }, (300 - this.level * 30))
    }
  },
  mounted() {
    window.onresize = this.init;
    this.init();
    document.addEventListener('keydown', (event) => {
      this.directionChange(event);
    })
    this.move();
  },
  computed: {
    stageStyle() {
      return { width: this.stageWidth + 'px', height: this.stageHeight + 'px' }
    },
    infoStyle() {
      let height = ~~((this.boxHeight - 20) * 0.05 / this.unit) * this.unit + 'px';
      const margin = parseInt(height) / 2 + 'px'
      return { height, 'font-size': height, margin }
    },
    foodStyle() {
      let width = this.unit + 'px';
      let height = this.unit + 'px';
      let left = this.foodSituation.left + 'px';
      let top = this.foodSituation.top + 'px'
      return { width, height, left, top }
    },
    sliceStyle() {
      let width = this.unit / 2 + 'px';
      let height = this.unit / 2 + 'px';
      return { width, height }
    },
    snakeHeadStyle() {
      let width = this.unit + 'px';
      let height = this.unit + 'px';
      let left = this.snakeSituation.left + 'px';
      let top = this.snakeSituation.top + 'px'
      return { width, height, left, top ,backgroundColor:'red'}
    },
  }
}
</script>

<style scoped lang="less">
.retroSnake {
  width: 100%;
  height: 100%;
}

.gameBox {
  width: 100%;
  height: 100%;
  background: #b7d4a8;
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  flex-flow: column;

  .stage {
    border: 2px solid black;
    margin: 0px auto;
    position: relative;

    .snake {
      display: flex;

      >div {
        position: absolute;
        background: black;
        border: 1px solid #b7d4a8;
        box-sizing: border-box;
      }
    }

    .food {
      position: absolute;
      display: flex;
      flex-flow: wrap;

      .slice {
        background: black;
        transform: rotate(45deg);
      }
    }
  }

  .info {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  .handler {
    flex: 1;
    display: flex;
    flex-flow: column;

    >div {
      flex: 1;
      display: flex;
      align-items: center;
      justify-content: center;

      .btnBox {
        box-shadow: 0 1px 5px 0px black;
        border-radius: 10px;
        overflow: hidden;
        display: flex;
        align-items: center;
        justify-content: center;
      }

    }
  }
}
</style>