<template>
  <div class="snake wrapper pt">
    <van-nav-bar title="贪吃蛇" left-arrow fixed @click-left="onClickLeft">
    </van-nav-bar>
    <RetroSnake :key="key" @childReload="childReload"></RetroSnake>
  </div>
</template>

<script>
import RetroSnake from '@/components/Mini/RetroSnake.vue';
export default {
  name: 'SnakePage',
  inject:['reload'],
  components: {
    RetroSnake
  },
  data() {
    return {
      key:0,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    childReload() {
      this.key=this.reload()
    }
  },
  mounted() {
  }
}
</script>

