<template>
    <div class="My wrapper pbt">
        <div class="logined noLogin bulk myBulk" v-if="logined">
            <div>
                <div class="content">
                    <div class="imgbox">
                        <van-image round width="2rem" height="2rem" :src="avatarGet(user.avatar)" fit='cover' lazy-load
                            icon-size="2rem" :loading-icon="loadingIcon()" />
                    </div>
                    <div class="text">
                        <p class="p3">{{ inited ? user.nickname : '用户' + userId }}
                            <van-icon class-prefix="iconfont icon-nan" size="0.5rem" v-show="user.sex === '男'"
                                color="blue" />
                            <van-icon class-prefix="iconfont icon-nv" size="0.5rem" v-show="user.sex === '女'"
                                color="pink" />
                        </p>
                        <p class="p6g van-multi-ellipsis--l2">{{ inited ? user.signature : '这个人很懒，什么也没说' }}</p>
                    </div>
                </div>
            </div>
            <div v-if="this.$store.state.user">
                <van-grid clickable :column-num="3" :border="false">
                    <van-grid-item :to="'/subscribe/' + this.$store.state.user.userId + '/' + '0'">
                        <p class="p2">{{ follow.length }}</p>
                        <p class="p5g">关注</p>
                    </van-grid-item>
                    <van-grid-item :to="'/subscribe/' + this.$store.state.user.userId + '/' + '1'">
                        <p class="p2">{{ fan.length }}</p>
                        <p class="p5g">粉丝</p>
                    </van-grid-item>
                    <van-grid-item>
                        <p class="p2">{{ inited ? user.likeCount : 0 }}</p>
                        <p class="p5g">获赞</p>
                    </van-grid-item>
                </van-grid>
            </div>
            <div class="cah baseData">
                <van-grid clickable :column-num="2" :border="false">
                    <van-grid-item icon="home-o" to="/myCollect">
                        <van-cell title="我的收藏" is-link icon="star" />
                    </van-grid-item>
                    <van-grid-item icon="home-o" to="/history">
                        <van-cell title="历史记录" is-link icon="clock" />
                    </van-grid-item>
                </van-grid>
            </div>

            <div class="listCell">
                <van-tabs v-model:active="active" swipeable shrink class="out" @change="onChange">
                    <van-tab title="数据">
                        <van-cell-group title="推荐服务">
                            <van-cell title="修改资料" to="/profileEdit" is-link>
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-xiugai" size="0.5rem" />
                                </template>
                            </van-cell>
                            <van-cell title="草稿箱" to="/draftList" is-link>
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-caogao" size="0.5rem" />
                                </template>
                            </van-cell>
                            <van-cell is-link @click="download()">
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-xiazai" size="0.5rem" />
                                </template>
                                <template #title>
                                    更新/下载App
                                    <van-tag type="danger" v-if="newVersion.length">新</van-tag>
                                </template>
                            </van-cell>
                            <van-cell title="问题反馈" to="feedback" is-link>
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-31wentifankui" size="0.5rem" />
                                </template>
                            </van-cell>
                            <van-cell title="实验室" to="miniList" is-link>
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-shiyanshi" size="0.5rem" color="#39f" />
                                </template>
                            </van-cell>
                            <van-cell title="设置" is-link to="/setup">
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-shezhi" size="0.5rem" />
                                </template>
                            </van-cell>
                        </van-cell-group>
                        <van-cell-group title="其他服务">
                            <van-cell title="商务合作" to="contact" is-link>
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-jiarukequn" size="0.5rem" />
                                </template>
                            </van-cell>
                            <van-cell title="加入我们" to="contact" is-link>
                                <template #icon>
                                    <van-icon class-prefix="iconfont icon-CPhezuo_o" size="0.5rem" />
                                </template>
                            </van-cell>
                        </van-cell-group>
                        <van-button type="danger" block @click="loginOut">退出登录</van-button>
                    </van-tab>
                    <van-tab title="动态">
                        <div v-show="!inited">
                            <div v-for="el in 2 " :key="el" class="avatarSkeleton">
                                <van-skeleton title :row="0" avatar title-width="35%" />
                                <van-skeleton title class="avatarSkeleton" />
                                <van-skeleton :row="3" />
                            </div>
                        </div>
                        <ListCell :submitIdArr="submit" :postIdArr="post" :commentArr="comment" :userId="userId - 0"
                            v-if="inited" @refresh="refresh" :key="key">
                        </ListCell>
                    </van-tab>
                </van-tabs>
            </div>
        </div>
        <div class="noLogin" v-else>
            <span class="p1">个人中心</span>
            <div>
                <router-link to="/login" class="content">
                    <div class="imgbox">
                        <van-image round width="2rem" height="2rem" :src="require('@/assets/images/notlogin.png')"
                            lazy-load>
                        </van-image>
                    </div>
                    <div class="text">
                        <p class="p3">点击登录</p>
                        <p class="p6g">登录解锁更多精彩内容~</p>
                    </div>
                </router-link>
            </div>
        </div>
        <TabBar></TabBar>
        <van-action-sheet v-model:show="show" :actions="actions" @select="onSelect"
            :description="'选择版本' + '(' + '当前' + window.version + ')'" cancel-text="取消" close-on-click-action />
    </div>
</template>

<script>
import { Dialog, Toast } from 'vant';
import ListCell from '@/components/ListCell.vue'
import TabBar from '@/views/layout/TabBar.vue'
export default {
    name: 'MyView',
    components: {
        ListCell, TabBar
    },
    inject: ['reload', 'developing', 'avatarGet', 'loadingIcon'],
    data() {
        return {
            src: '',
            logined: false,
            active: 0,
            active1: 0,
            user: {},
            fan: [],
            follow: [],
            submit: [],
            post: [],
            comment: [],
            inited: false,
            key: 0,
            scrollLocation: 0,
            show: false,
            actions: [
                { name: 'android' },
                { name: 'ios' },
            ],
            newVersion: '',
            apkName: '',
            updateContent: '',
            window: window
        }
    },
    methods: {
        loginOut() {
            Dialog.confirm({
                title: '退出登录',
                message:
                    '确定吗?',
            })
                .then(() => {
                    this.$store.commit('setUser', null)
                    // this.logined = false;
                    location.reload(true);
                })
                .catch(() => {
                    // on cancel
                });
        },
        async onSelect(item) {
            if (item.name === 'ios') {
                this.developing()
                return
            }
            else {
                if (this.newVersion.length)
                    Dialog.confirm({
                        title: '更新确认' + '  ' + window.version + '=>' + this.newVersion,
                        message: this.updateContent,
                        confirmButtonText: '下载'
                    })
                        .then(() => {
                            const elink = document.createElement('a')
                            elink.href = this.$store.state.URL + '/version/apk/' + this.apkName
                            elink.setAttribute('download', 'DINOEYE')
                            elink.style.display = 'none'
                            document.body.appendChild(elink)
                            setTimeout(() => {
                                elink.click()
                                document.body.removeChild(elink)
                            }, 66)
                        })
                        .catch(() => {
                            this.show = false;
                        });
                else {
                    Toast('已是最新版本!')
                }
            }
        },
        versionCheck() {
            this.$axios.get('versionCheck').then(rs => {
                this.apkName = rs[0].at(-1);
                let version = (rs[1].match(/v.{5}v/)[0]).replaceAll('v', '');
                if (version > window.version) {
                    this.newVersion = version;
                    this.actions[0].name = this.actions[0].name + '(' + this.newVersion + ')'
                    const content = rs[1].match(/-\d.+-/g);
                    this.updateContent = content.toString().replaceAll(',', '  ')
                }
            })
        },
        download() {
            this.show = true;
        },
        async refresh(data, callback) {
            await this.dataShow()
            callback(data)
        },
        async dataShow() {//数据加载
            const params = { userId: this.$store.state.user.userId }
            await this.$axios.get('userDataShow', { params }).then(rs => {
                this.user = rs;
                this.fan = rs.fans.fan;
                this.follow = rs.follows.follow;
                this.submit = rs.submits.submit.reverse();
                this.post = rs.posts.post.reverse();
                this.comment = rs.comments.comment.reverse();
                this.inited = true;
            })
        },
        scrollHandler() {
            if (this.active) {
                this.scrollLocation = document.querySelector('.myBulk').scrollTop;
            }
        },
        onChange(name) {
            this.$nextTick(() => {
                if (name)
                    document.querySelector('.myBulk').scrollTop = this.scrollLocation;
            })
        }
    },
    mounted() {
        console.log('hello');
        if (this.$store.state.user) {
            this.$nextTick(() => {
                document.querySelector('.myBulk').addEventListener('scroll', this.scrollHandler, { passive: false })
            })
            this.logined = true;
            this.userId = this.$store.state.user.userId
            this.dataShow();
            //检查是否有新版本
            this.versionCheck()
        }
        else {
            this.logined = false
        }

    },
    activated() {
        this.$nextTick(() => {
            if (this.active)
                document.querySelector('.myBulk').scrollTop = this.scrollLocation;
        })
        if (this.$route.meta.isBack !== true) {
            // console.log('刷新');
            // Object.assign(this.$data, this.$options.data())
        }
        else {
            const params = { userId: this.$store.state.user.userId }//是从资料编辑返回就更新用户信息
            this.$axios.get('userDataShow', { params }).then(rs => {
                this.user = rs;
            })
        }
    },
    beforeRouteEnter(to, from, next) {
        if (from.path.indexOf('/profileEdit') !== -1) { to.meta.isBack = true; }
        next()
    },

}
</script>

<style scoped lang="less">
.noLogin {
    >span {
        display: block;
        height: 46px;
        line-height: 46px;
        margin-left: 10px;
        position: fixed;
        top: 0;
        z-index: 100;
    }

    .content {
        padding: 10px;
        display: flex;
        align-items: center;

        >div {
            height: 100%;

            &.text {
                padding-left: 15px;
            }
        }
    }
}

.avatarSkeleton {
    margin: 10px 0;
}

.logined {
    .baseData {
        border-top: 4px solid #f2f4f3;
    }

    :deep(.cah .van-grid-item__content) {
        padding: 0;
    }

    :deep(.out>.van-tabs__wrap) {
        position: fixed;
        z-index: 100;
        top: 0;
    }

    :deep(.van-cell-group .van-cell__title) {
        margin-left: 10px;
    }

    .listCell {
        border-top: 2px solid #f2f4f3;
    }
}
</style>