<template>
  <div class="enWordMemory wrapper">
    <div class="listBox">
      <div class="list">
        <van-cell-group inset>
          <template #title>
            <div style="display:flex;justify-content:space-between">
              <span>单词列表</span>
              <van-button size="mini" color="transparent" @click="this.show = true">
                <template #icon>
                  <van-icon name="ellipsis" color="#aaa" size="0.5rem"></van-icon>
                </template>
              </van-button>
            </div>
          </template>
          <div :style="{ height: height + 'px' }" class="bulk" ref="bulk">
            <div class="swipeBox" v-if="!writingFrMem">
              <van-swipe-cell v-for="el in words" :key="el.id">
                <van-cell :style="{ background: integrityCheck(el) ? 'red' : 'rgb(210, 247, 136)' }">
                  <template #title>
                    <div @click="editWord(el.id)" style="display:flex">
                      <span style="padding-right:10px;">{{ el.id > 9 ? el.id : '0' + el.id }}.</span>
                      <van-field style="padding:0!important" v-model="el.word" :ref="'wordInput' + el.id"
                        v-if="el.id == target.id && target.state && target.state === 'word'" input-align="left"
                        @blur="onBlur" @keyup.enter="onBlur" />
                      <span v-else :style="{ color: integrityCheck(el) ? 'white' : 'black' }">{{ el.word.length ?
                          el.word :
                          '单词不见了'
                      }}</span>
                    </div>
                  </template>
                  <template #value>
                    <div @click="editMean(el.id)" style="display:flex;justify-content: flex-end;">
                      <van-field style="padding:0!important" v-model="el.mean" :ref="'meanInput' + el.id"
                        v-if="el.id == target.id && target.state && target.state === 'mean'" input-align="right"
                        @blur="onBlur" @keyup.enter="onBlur" />
                      <span v-else :style="{ color: integrityCheck(el) ? 'white' : 'grey' }">{{ el.mean.length ? el.mean
                          : '暂无释义'
                      }}</span>
                    </div>
                  </template>
                </van-cell>
                <template #right>
                  <van-button square type="danger" text="删除" @click="delWord(el.id)" style="height:100%" />
                </template>
              </van-swipe-cell>
            </div>
            <div v-else class="writingFrMem">
              <p>{{ (readyWords[index].id > 9 ? '' : '0') + readyWords[index].id + '-' + readyWords[index].mean }}</p>
            </div>
          </div>
        </van-cell-group>
      </div>
    </div>
    <div class="box" ref="box">
      <div class="wordInput">
        <van-cell-group inset :title="!this.writingFrMem ? '听写ing' : '默写ing'">
          <van-field v-model="newWords" label="单词" placeholder="请输入单词" style="background:#eee" clearable
            @keyup.enter="submit" />
        </van-cell-group>
        <div class="btnBox" style="padding:10px;">
          <van-button type="primary" block round @click="submit">确定</van-button>
        </div>
      </div>
    </div>
    <van-action-sheet v-model:show="show" :actions="actions" cancel-text="取消" close-on-click-action
      @select="onSelect" />
    <van-popup v-model:show="showPop" :style="{ height: '100%' }" closeable close-icon="arrow-down"
      close-icon-position="top-left" position="bottom">
      <div style="display:flex;justify-content:center;align-items:center;padding-top:2rem;" >
        <table border='1' cellspacing='20'  v-if="mistake.length">
            <caption>复盘</caption>
            <thead>
              <tr>
                <th style="color:blue">单词释义</th>
                <th style="color:green">正确答案</th>
                <th style="color:red">我的答案</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="el in mistake" :key="el.id">
                <td style="color:blue">{{el.mean}}</td>
                <td style="color:green">{{el.word}}</td>
                <td style="color:red">{{el.mistake}}</td>
              </tr>
            </tbody>
        </table>
        <van-empty v-else image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png" image-size="80"
          description="完全正确" />
      </div>
      <van-button type="primary" block @click="this.$router.back()" style="margin-top:1rem;">退出</van-button>
    </van-popup>
  </div>
</template>

<script>
import { useRect } from '@vant/use';
import { Dialog, Toast } from 'vant';
export default {
  name: 'EnWordMemory',
  data() {
    return {
      words: [],
      readyWords: [],
      mistake: [],
      newWords: '',
      height: null,
      target: { id: -1 },
      show: false,
      actions: [{ name: '开始默写', disabled: false }, { name: '开始听写', disabled: true }],
      integrityChecked: false,
      writingFrMem: false,
      index: 0,
      showPop: false,
    }
  },
  methods: {
    integrityCheck(el) {
      return !(el.word.trim().length && el.mean.trim().length) && this.integrityChecked
    },
    // 添加单词
    addWord() {
      if (this.newWords.trim().length) {
        const item = {};
        item.word = this.newWords;
        item.id = this.words.length ? this.words.at(-1).id + 1 : 1
        item.mean = '';
        this.words.push(item);
        this.newWords = '';
        this.$nextTick(() => {
          this.$refs.bulk.scrollTop = this.$refs.bulk.scrollHeight
        })
      }
    },
    check() {//默写时检查
      if (this.newWords.trim().length) {
        if (this.newWords.trim() !== this.readyWords[this.index].word) {
          Toast.fail({
            message: '错误!',
            icon: 'cross',
          })
          const obj = { ...this.readyWords[this.index], mistake:this.newWords.trim()}
          this.mistake.push(obj)
        }
        this.newWords = '';
        if (this.index === this.readyWords.length - 1) {
          //结束了
          this.showPop = true;
        }
        else
          this.index++;
      }
    },
    submit() {
      if (!this.writingFrMem)
        this.addWord();
      else
        this.check();
    },
    setHeight() {
      const boxTop = useRect(this.$refs.box).top
      const bulkTop = useRect(this.$refs.bulk).top
      console.log(boxTop - bulkTop);
      this.height = boxTop - bulkTop
    },
    delWord(id) {
      this.words = this.words.filter(el => el.id !== id)
    },
    editWord(id) {
      this.target = this.words.find(el => el.id === id);
      this.target.state = 'word';
      this.$nextTick(() => {
        this.$refs['wordInput' + id][0].focus();
      })
    },
    editMean(id) {
      this.target = this.words.find(el => el.id === id);
      this.target.state = 'mean';
      this.$nextTick(() => {
        this.$refs['meanInput' + id][0].focus();
      })
    },
    onBlur() {
      this.target = { id: -1 };
    },
    onSelect(item) {
      if (item.name === '开始默写') {
        const arr = [];
        this.words.forEach((el) => {
          if (!el.word.trim().length || !el.mean.trim().length)
            arr.push(el.id);
        })
        if (arr.length) {
          this.integrityChecked = true;
          Toast('存在释义或单词不完整的项目')
          return
        }
        Dialog.confirm({
          title: '进入默写',
          message:
            '共有' + this.words.length + '个单词会进行默写,确定吗',
        })
          .then(() => {
            //改变状态
            this.writingFrMem = true;
            this.actions.forEach(el => {
              if (el.name === '开始默写')
                el.disabled = true;
              else
                el.disabled = false;
            })
            //打乱数组
            this.readyWords.length = 0;
            this.words.forEach(el => {
              this.readyWords.push(el)
            });
            this.readyWords.sort(() => {
              return 0.5 - Math.random(0, 1)
            })
            const arr = this.readyWords.map(el => el.id)
            console.log(arr);
          })
          .catch(() => {
            // on cancel
          });
      }
      else if (item.name === '开始听写') {
        Dialog.confirm({
          title: '退出默写',
          message:
            '退出默写后下次需要重新开始,确定吗',
        })
          .then(() => {
            //改变状态
            this.writingFrMem = false;
            this.actions.forEach(el => {
              if (el.name === '开始听写')
                el.disabled = true;
              else
                el.disabled = false;
            })

          })
          .catch(() => {
          });
      }
    }
  },
  mounted() {
    this.$nextTick(() => {
      this.setHeight()
    })
  }
}
</script>

<style scoped lang="less">
table{
  border-collapse: collapse;
  text-align: center;
}
.box {
  position: fixed;
  bottom: 10vh;
  padding: 20px;

  .wordInput {
    border-radius: 10px;
    box-shadow: 0 0 5px 0px rgb(255, 98, 98);
    padding: 10px 0;
    width: 100%;
  }
}

.listBox {
  padding: 20px;

  .list {
    border-radius: 10px;
    box-shadow: 0 0 5px 0px grey;
    // background: rgb(255, 255, 187);
  }
}

.bulk {
  margin-bottom: 10px;
  background: rgb(229, 247, 192);

  border-radius: 10px;
  // background: rgb(255, 255, 187);
}

.writingFrMem {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
</style>