<template>
    <div class="wrapper pt">
        <van-nav-bar fixed title='App下载' ref="top" />
        <div class="main" :style="{ height: mainHeight + 'px' }" ref="top">
            <van-divider />
            <van-skeleton title :row="2" :loading="loading" title-width="20%">
                <van-cell-group title="版本" inset>
                    <van-cell title="版本号" :value="newVersion" />
                    <van-cell title="更新时间" :value="date" />
                </van-cell-group>
            </van-skeleton>
            <van-divider />
            <van-skeleton title :row="7" :loading="loading" title-width="20%">
                <van-cell-group title="更新内容" inset>
                    <van-cell :title="el" v-for="(el) in content" :key="el">
                    </van-cell>
                </van-cell-group>
            </van-skeleton>
            <van-divider />
            <van-skeleton title :row="4" :loading="loading" title-width="40%">
            </van-skeleton>
        </div>
        <div class="bottom" ref="bottom">
            <div>
                <van-button type="primary" color="black" @click="download">App下载(android)</van-button>
            </div>
            <div>
                <van-button type="primary" color="green" @click="goGit">网页版</van-button>
            </div>
        </div>
    </div>
</template>

<script>
import { useRect } from '@vant/use';
export default {
    name: 'AppDownload',
    components: {
    },
    data() {
        return {
            newVersion: '',
            apkName: '',
            content: [],
            date: '',
            indexArr: [0, 4, 6, 8, 10, 12, 14],
            mainHeight: 0,
            loading: true,
        }
    },
    methods: {
        versionCheck() {
            this.$axios.get('versionCheck').then(rs => {
                this.apkName = rs[0].at(-1);
                const str = this.apkName.match(/202.{11}/)[0];
                for (let i in this.indexArr) {
                    this.date = this.date + str.slice(this.indexArr[i], this.indexArr[+i + 1])
                    if (i < 2)
                        this.date = this.date + '/'
                    else if (+i === 2)
                        this.date = this.date + ' '
                    else if (+i === 3 || +i === 4)
                        this.date = this.date + ':'
                }
                let version = (rs[1].match(/v.{5}v/)[0]).replaceAll('v', '');
                this.newVersion = version;
                this.content = rs[1].match(/-\d.+-/g);
                setTimeout(() => {
                    this.loading = false
                }, 2000)
            })
        },
        download() {
            const elink = document.createElement('a')
            elink.href = this.$store.state.URL + '/version/apk/' + this.apkName
            elink.setAttribute('download', 'DINOEYE')
            elink.style.display = 'none'
            document.body.appendChild(elink)
            setTimeout(() => {
                elink.click()
                document.body.removeChild(elink)
            }, 66)
        },
        goGit() {
            location = this.$store.state.gitUrl
        },
        heightGet() {
            this.$nextTick(() => {
                const top = useRect(this.$refs.top).top
                this.mainHeight = useRect(this.$refs.bottom).top - top
            })
        }
    },
    created() {
    },
    mounted() {
        this.versionCheck();
        this.heightGet();
        window.onresize = this.heightGet
    },

}
</script>

<style scoped lang="less">
.main {
    overflow: auto;
}

.bottom {
    position: fixed;
    z-index: 100;
    bottom: 0;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-flow: column;
    box-shadow: 0 0 5px 0 grey;
    border-radius: 20px 20px 0 0;
    background-color: #fff;

    >div {
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 10px;
    }
}
</style>