<template>
  <div class="musicRadio wrapper ">
    <div id="player"></div>
    <van-action-sheet v-model:show="show" :actions="actions" cancel-text="取消" description="上传内容" close-on-click-action
      @select="onSelect" />
    <van-popup v-model:show="popShow" position="top" :style="{ width: '100%' }" round>
      <div class="uploadBox">
        <van-uploader :after-read="afterRead" v-model="fileList" :max-count="1" :before-read="beforeRead">
          <div class="select">
            <van-button icon="plus" type="primary">选择文件</van-button>
          </div>
        </van-uploader>
        <div class="select">
          <p class="p5" v-show="!upSong">你将为 <span class="bolder">{{ this.targetSong ? this.targetSong.name : ''
          }}</span>上传歌词</p>
          <p class="p5" v-if="!fileList.length">
            请选择{{ upSong ? '.mp3' : '.lrc' }}文件
          </p>
          <p v-else class="p5 ">你选择了 <span>{{ mp3 ? mp3.file.name : '' }}</span></p>
        </div>
        <van-button icon="circle" color="black" @click="songSubmit" v-if="fileList.length">提交</van-button>
      </div>
    </van-popup>
    <van-popup v-model:show="audioListShow" position="bottom" :style="{ width: '100%', height: '70%' }" round>
      <van-nav-bar title="那么，你要为哪首歌上传歌词呢?">
      </van-nav-bar>
      <div class="cellBox">
        <van-cell v-for="el, index in audio" :key="el" is-link center @click="upLrc(el)">
          <template #title>
            <div class="title">
              <span class="p3">{{ index + 1 }}</span>
              <div>
                <div class="songTitle p5">
                  <van-tag :type="el.lrc ? 'success' : 'danger'" :round="el.lrc ? true : false">{{ el.lrc ? '有歌词' :
                      '无歌词'
                  }}</van-tag>&nbsp;
                  <span>{{ el.name }}</span>
                </div>
                <span class="singer p5-5g">{{ el.artist }}</span>
              </div>
            </div>
          </template>
        </van-cell>
      </div>
    </van-popup>
  </div>
</template>

<script>
import 'aplayer/dist/APlayer.min.css';
import APlayer from 'aplayer';
import { Toast } from 'vant';
export default {
  name: 'MusicRadio',
  components: {
  },
  data() {
    return {
      audio: [],
      show: false,
      popShow: false,
      audioListShow: false,
      mp3: null,
      lrc: null,
      upSong: true,
      fileList: [],
      targetSong: null,
      actions: [
        { name: '上传歌曲', subname: 'mp3文件' },
        { name: '上传歌词', subname: 'lrc文件' },
      ]
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    showAction() {
      this.show = true;
    },
    onSelect(item) {
      if (this.actions.indexOf(item)) {
        this.audioListShow = true;
      }
      else {
        this.popShow = true;
        this.upSong = true;
      }
    },
    upLrc(el) {
      this.targetSong = el;
      this.upSong = false;
      this.audioListShow = false;
      this.popShow = true;
    },
    afterRead(file) {
      this.mp3 = file;
    },
    beforeRead(file) {
      const suffix = file.name.slice(-4)
      if (this.upSong) {
        if (suffix !== '.mp3') {
          Toast('上传歌曲请选择.mp3文件');
          return false;
        }
      }
      else {
        if (suffix !== '.lrc') {
          Toast('上传歌词请选择.lrc文件');
          return false;
        }
      }
      return true;
    },
    async songSubmit() {
      Toast.loading({
        message: '提交中',
        overlay: true,
        duration: 0
      })
      const data = new FormData()
      data.append('mp3', this.mp3.file);
      let response;
      if (this.upSong) {
        response = await this.$axios.post('audioUpload', data)
      }
      else {
        data.append('songName', this.targetSong.url.split('/').at(-1))
        response = await this.$axios.post('lrcUpload', data)
      }
      if (response === 'ok') {
        Toast.success({
          message: '上传成功!',
          overlay: true,
          onClose: () => {
            this.$emit('childReload')
          }
        });
      }
      else {
        Toast.fail('出错啦' + response)
      }
    },
    async initAudio() {
      await this.audioListGet();
      const ap =
        new APlayer({
          container: document.getElementById('player'),
          mini: false,
          autoplay: false,
          theme: '#111',
          loop: 'all',
          order: 'random',
          preload: 'auto',
          volume: 0.7,
          mutex: true,
          listFolded: false,
          listMaxHeight: 90,
          lrcType: 1,
          audio: []
        });
      if (this.audio.length)
        ap.list.add(this.audio);
      this.$nextTick(() => {
        this.$emit('loaded');
      })
    },
    async audioListGet() {
      await this.$axios.get('audioListGet').then(res => {
        // console.log(res);
        this.audio = res.map(el => {
          let cover = ''
          if (el.cover.length)
            cover = el.cover
          const name = el.title
          const artist = el.artist
          const url = this.$store.state.URL + 'upload/audio/song/' + el.url;
          const lrc = el.lrc
          return { artist, cover, name, url, lrc }
        })
      })
    }
  },
  mounted() {
    this.initAudio()
  }
}
</script>

<style scoped lang="less">
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}

.uploadBox {
  .center;
  flex-flow: column;
  padding: 50px;

  .select:extend(.center) {
    flex-flow: column;

    p {
      text-align: center;
      padding: 4px;
    }
  }
}

.cellBox {
  .title {
    .center;
    justify-content: flex-start;

    >span {
      padding-right: 20px;
    }
  }
}
</style>