<template>
  <div class="replyList wrapper pt">
    <van-nav-bar title="评论及回复" left-arrow @click-left="onClickLeft" fixed />
    <div class="bulk">
      <div v-show="loading">
        <div v-for="el in 3 " :key="el" style="margin:10px 0">
          <van-skeleton title avatar :row="0" avatar-size=".8rem" title-width="25%" />
          <van-skeleton :row="1" style="margin:10px 0;width:100%" row-width="100%" />
          <van-skeleton title avatar :row="0" avatar-size="1rem" title-width="100%" avatar-shape="square" />
        </div>
      </div>
      <van-empty description="暂无回复" v-show="!loading && !list.length" />
      <CommentList :ArticleList="list" :notice="true"></CommentList>
    </div>
  </div>
</template>

<script>
import CommentList from '@/components/CommentList.vue'
export default {
  name: 'ReplyList',
  components: {
    CommentList,
  },
  inject: ['getId', 'before'],
  data() {
    return {
      list: [],
      loading: true,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back()
    }
  },
  mounted() {
    const userId = this.getId(this.$route.path);
    const params = { userId, type: 'commentNotice' }
    this.$axios.get('getMessageNotice', { params }).then(rs => {
      console.log(rs);
      this.list = rs.filter(el => {
        return el !== null
      })
      this.loading = false;
    })
  },

}
</script>

