<template>
  <div class="musicPage wrapper pt">
    <van-nav-bar title="音乐电台" left-arrow fixed @click-left="onClickLeft" @click-right="onClickRight">
      <template #right>
        <van-button icon="ellipsis" color="transparent"></van-button>
      </template>
    </van-nav-bar>
    <van-popup v-model:show="show"
      :position="(this.$store.state.setup && this.$store.state.setup.system) ? 'top' : 'bottom'"
      :close-on-click-overlay="false">
      <div class="fieldBox">
        <van-field v-model="pwd" center clearable label="口令" placeholder="请输入口令" @keyup.enter="check">
          <template #button>
            <van-button size="small" type="primary" @click="check">提交</van-button>
          </template>
        </van-field>
      </div>
    </van-popup>
    <div class="box" v-show="loading && passed">
      <van-loading size="24px" vertical color="#1989fa">加载中...</van-loading>
    </div>
    <div class="box" v-if="!passed">
      <van-empty image="https://fastly.jsdelivr.net/npm/@vant/assets/custom-empty-image.png" image-size="80"
        description="暂无内容" />
    </div>
    <APlayer class="bulk" @loaded="loaded" v-else ref="APlayer" :key="key" @childReload="childReload"></APlayer>
  </div>
</template>

<script>
import { Toast } from 'vant'
import APlayer from '@/components/Mini/MusicRadio.vue'
import dayjs from '@/assets/util/public'
export default {
  name: 'MusicPage',
  components: {
    APlayer
  },
  inject:['reload'],
  data() {
    return {
      pwd: '',
      passed: true,
      show: false,
      loading: true,
      key:0,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    onClickRight() {
      this.$refs.APlayer.showAction()
    },
    check() {
      const password = dayjs().hour() * dayjs().minute()
      if (+this.pwd.trim() === password) {
        this.show = false;
        this.passed = true;
      }
      else {
        Toast.fail('口令错误')
      }
    },
    loaded() {
      setTimeout(() => {
        this.loading = false;
      }, 1000)
    },
    childReload() {
      this.key = this.reload();
    }
  },
  mounted() {
  }
}
</script>

<style scoped lang="less">
.fieldBox {
  width: 100%;
  box-sizing: border-box;
  height: 3rem;
  display: flex;
  justify-content: center;
  align-items: center;
  box-shadow: 0 0 5px 0;
  padding: 20px 30px;
  background: rgb(193, 193, 193);
}

.box {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>