<template>
  <div class="miniList wrapper pt">
    <van-nav-bar title="实验室" left-arrow fixed @click-left="onClickLeft">
    </van-nav-bar>
    <van-cell title="英语学习" is-link to="EWMPage" />
    <!-- @click="goodnight" -->
    <van-cell title="音乐电台" is-link to="/MusicPage" />
    <van-cell title="贪吃蛇" is-link to="snakePage" />
  </div>
</template>

<script>
export default {
  name: 'MiniList',
  inject: ['developing'],
  data() {
    return {
      index: 0,
    }
  },
  methods: {
    onClickLeft() {
      this.$router.back();
    },
    goodnight() {
      this.index++;
      if (this.index >= 5)
        this.$router.push('/MusicPage')
      else
        this.developing();
    }
  },
  mounted() {
  }
}
</script>

